import React, { useState } from 'react';
import styled from 'styled-components';
import TemperatureSlider from '../../../components/temperatureSlider/temperatureSlider';
import WaterSlider from '../../../components/waterSlider/waterSlider';
import VoiceControlBtn from '../../../components/voiceControlBtn/voiceControlBtn';
import Guide from './guide';
import { appSize } from '../../../assets/style/style';

const TemperaturePageWrapper = styled.div`
  background-color: white;
  height: ${appSize.contentHeight};
  min-width: ${appSize.appMinWidth};
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  position: relative;
`;

function TemperaturePage() {
  const [temperature, setTemperature] = useState(30);

  return (
    <TemperaturePageWrapper>
      <Guide />
      <TemperatureSlider
        temperature={temperature}
        setTemperature={setTemperature}
      />
      <WaterSlider />
      <VoiceControlBtn
        temperature={temperature}
        setTemperature={setTemperature}
      />
    </TemperaturePageWrapper>
  );
}

export default TemperaturePage;
