import React from 'react';
import styled from 'styled-components';
import { DragOutlined } from '@ant-design/icons';
import GridLayout, { WidthProvider } from 'react-grid-layout';
import WeatherWidget from '../../../components/weatherWidget/weatherWidget';
import MusicWidget from '../../../components/musicWidget/musicWidget';
import { API } from '../../../assets/const';
import './index.less';

const GridLayoutWithWidth = WidthProvider(GridLayout);

const DragBtn = styled(DragOutlined)`
  position: absolute;
  left: 0;
  z-index: 1;
  height: 20px;
  width: 20px;
  font-size: 20px;
  color: white;
  background: rgba(0,0,0,0.4);
`;

function WidgetPage() {
  const layout = [
    { i: 'weather', x: 0, y: 0, w: 6, h: 16, isResizable: false },
    { i: 'music', x: 6, y: 0, w: 6, h: 12, isResizable: false }
  ];

  return (
    <div className='widget-page-wrapper'>
      <GridLayoutWithWidth className="layout" layout={layout} cols={12} rowHeight={30}>
        <div onTouchMove={e => e.stopPropagation()} key="weather">
          <DragBtn />
          <WeatherWidget apiKey={API.OPENWEATHERMAP_API_KEY} />
        </div>
        <div onTouchMove={e => e.stopPropagation()} key="music">
          <DragBtn />
          <MusicWidget />
        </div>
      </GridLayoutWithWidth>
    </div>
  );
}

export default WidgetPage;
