import React, { useContext } from 'react';
import styled, { keyframes } from 'styled-components';
import { observer } from 'mobx-react';
import { Input } from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import Section from '../../../../components/section/section';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import { avatars } from '../../../../assets/img/index';

const StyledInput = styled(Input)`
  width: 100%;
  height: 40px;
  .ant-input-group-addon {
    font-size: 20px;
    font-weight: bold;
    color: gray;
    width: 150px;
  }
  .ant-input {
    font-size: 20px;
  }
`;

const StyledPWInput = styled(Input.Password)`
  width: 100%;
  height: 40px;
  input {
    font-size: 20px;
  }
`;

const StyledSearch = styled(StyledInput.Search)`
  .ant-input-group-addon {
    font-size: 20px;
    font-weight: bold;
    color: gray;
    width: 150px;
  }
  .ant-input {
    font-size: 20px;
  }
`;

const PlayListWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const PlayListTitle = styled.h1`
  color: #000;
  margin: 0;
`;

const PlayListMask = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: black;
  top: 0;
  text-align: center;
  vertical-align: center;
`;

const rotateAnimation = keyframes`
  0% {transform: rotate3d(0, 1, 0, 0deg);}
  50% {transform: rotate3d(0, 1, 0, 180deg);}
  100% {transform: rotate3d(0, 1, 0, 360deg);}
`;

const CurrentCollectionCover = styled.img`
  position: relative;
  margin-left: 20px;
  margin-top: -10px;
  padding: 10px;
  width: 150px;
  box-shadow: 0px 2px 2px 2px rgba(0,0,0,0.2);
  animation: ${rotateAnimation} 2s linear;
  background: rgba(0,0,0,0.5);
`;

const CollectionWrapper = styled.div`
  position: relative;
  margin: 10px;
`;

const ProfileSection = observer(() => {
  const profileStore = useContext(ProfileStoreCtx);

  const renderProfile = (clientProfile: ClientProfile) => {
    return (
      <>
        <Section flexDirection="row">
          <img
            alt="avatar"
            style={{ height: 150, width: 150 }}
            src={avatars[clientProfile.iconId]}
          />
        </Section>
        <Section flexDirection="row">
          <StyledInput
            addonBefore="Client Name"
            value={clientProfile.client}
            onChange={e => {
              profileStore.updateClientName(e.target.value);
            }}
          />
        </Section>
        <Section flexDirection="row">
          <StyledPWInput
            placeholder="Change Password"
            iconRender={
              visible => (
                visible
                  ? <EyeTwoTone />
                  : <EyeInvisibleOutlined />
              )
            }
          />
        </Section>
        <Section flexDirection="row">
          <StyledSearch
            onSearch={(value: string) => {
              profileStore.searchAudioCollection(value);
            }}
            addonBefore="Search Track"
            disabled={profileStore.isLoading}
          />
        </Section>
        {profileStore.audioCollections.length > 0 ?
          <div>
            <PlayListMask
              onClick={e => {
                e.stopPropagation();
                profileStore.clearAudioCollection();
              }}
            />
            <Section flexDirection="column">
              <PlayListTitle> Select Your Track </PlayListTitle>
              <PlayListWrapper>
                {profileStore.audioCollections.map(
                  (playlist: AudioCollection) => {
                    return (
                      <CollectionWrapper
                        key={playlist.id}
                        onClick={e => {
                          e.stopPropagation();
                          profileStore.setAudioCollection(
                            playlist.id,
                            playlist.tracks[0].artwork_url
                          );
                        }}
                      >
                        <img
                          src={playlist.tracks[0].artwork_url}
                          alt='artwork'
                        />
                      </CollectionWrapper>
                    );
                  }
                )}
              </PlayListWrapper>
            </Section>
          </div>
          :
          <CurrentCollectionCover
            src={profileStore.currentCollection?.cover}
            alt='artwork'
          />}
      </>
    );
  };

  return (
    profileStore.isLogin
      ? renderProfile(profileStore.clientProfile as ClientProfile)
      : null
  );
});

export default ProfileSection;
