import React, { useContext } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../store/profileStore';
import { API } from '../../assets/const';

const MusicWidgetWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`;

const MusicWidget = observer(() => {
  const profileStore = useContext(ProfileStoreCtx);

  return (
    <MusicWidgetWrapper>
      <iframe
        title="sound_clould"
        width="100%"
        height="100%"
        scrolling="no"
        frameBorder="no"
        src={`${API.SOUND_CLOUD_WIDGET_BASE_API}${ 
          profileStore.currentCollection?.id 
        }&buying=false` +
          '&auto_play=false' +
          '&sharing=false' +
          '&download=false' +
          '&liking=false' +
          '&show_artwork=true' +
          '&show_comments=true' +
          '&show_playcount=true' +
          '&show_user=true' +
          '&hide_related=false' +
          '&visual=true' +
          '&start_track=0' +
          '&callback=true'}
      />
    </MusicWidgetWrapper>
  );
});

export default MusicWidget;
