import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { Card, Descriptions } from 'antd';
import {
  EditOutlined,
  DeleteOutlined,
  IdcardOutlined,
  CalendarOutlined,
  BgColorsOutlined,
  FundProjectionScreenOutlined,
  VideoCameraFilled,
  FireOutlined,
  MedicineBoxFilled,
  ClockCircleOutlined
} from '@ant-design/icons';
import ChildProfileForm from '../../../../components/forms/childProfileForm/childProfileForm';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import { avatars } from '../../../../assets/img/index';

const { Meta } = Card;

const ChildProfileWrapper = styled.div`
  display: flex;
  width:92%;
  overflow-x: auto;
  padding-left: 20px;
  &::-webkit-scrollbar {
    -webkit-appearance: none;
    margin-left: 20px;
  }

  &::-webkit-scrollbar:horizontal {
    height: 8px;
    margin-left: 20px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 8px;
    border: 2px solid white;
    background-color: rgba(0, 0, 0, .5);
    margin-left: 20px;
  }
  &::-webkit-scrollbar-track { 
    border-radius: 8px; 
    margin-left: 20px;
  }
`;

const ChildDetailWrapper = styled.div`
  margin: 20px;
  box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
`;

const ChildProfileDescription = styled(Descriptions)`
  padding: 0px;
  .ant-descriptions-item-content{
    padding: 10px 10px;
  }
  .ant-descriptions-item-label{
    padding: 10px 10px;
    background: lightgray;
    font-weight: 500;
  }
`;

const CardWrapper = styled.div`
  border: none;
  width: 150px;
  min-width: 150px;
  .ant-card-body{
    padding: 5px;
  }
  box-shadow: ${props => props.theme.isSelected ? '5px 5px 5px rgba(0, 0, 0, 0.2)' : ''};
  opacity: ${props => props.theme.isSelected ? 1 : 0.5};
  margin: 20px;
  overflow: hidden;
`;

const ChildProfileSection = observer(() => {
  const profileStore = useContext(ProfileStoreCtx);
  const [editingProfile, setEditing] = useState<null | ChildProfile>(null);

  const renderChildrenProfile = (childrenProfile: ChildProfile[]) => {
    return (
      <>
        <ChildProfileForm
          editingProfile={editingProfile}
          onEndEditing={() => setEditing(null)}
        />
        <ChildProfileWrapper
          onTouchMove={e => e.stopPropagation()}
        >
          {childrenProfile.map((childProfile: ChildProfile) => {
            return (
              <CardWrapper
                key={childProfile.id}
                theme={{
                  isSelected:
                    (profileStore.selectedChildProfile as ChildProfile).id
                    === childProfile.id
                }}
              >
                <Card
                  onClick={() => {
                    profileStore.setSelectedChildProfile(childProfile);
                  }}
                  cover={
                    <img
                      src={avatars[childProfile.iconId]}
                      style={{ height: 152, width: 152 }}
                      alt="avatar"
                    />
                  }
                  actions={[
                    <EditOutlined
                      key="edit"
                      onClick={() => setEditing(childProfile)}
                    />,
                    <DeleteOutlined
                      key="ellipsis"
                      onClick={(e) => {
                        profileStore.removeChildProfile(
                          childProfile.id as number
                        );
                        e.stopPropagation();
                      }}
                    />,
                  ]}
                >
                  <Meta
                    title={childProfile.name}
                  />
                </Card>
              </CardWrapper>
            );
          })}

        </ChildProfileWrapper>
        {profileStore.selectedChildProfile ?
          <ChildDetailWrapper>
            <ChildProfileDescription
              bordered
              layout="vertical"
              column={4}
            >
              <Descriptions.Item
                label={
                  <span>
                    <IdcardOutlined />
                    {` ${  profileStore.selectedChildProfile.name}`}
                  </span>
                }
              >
                <img
                  src={avatars[profileStore.selectedChildProfile.iconId]}
                  style={{ height: 50, width: 50 }}
                  alt="avatar"
                />
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><CalendarOutlined /> Birthday</span>}
              >
                {profileStore.selectedChildProfile.dob}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><BgColorsOutlined /> Color</span>}
              >
                {profileStore.selectedChildProfile.fColor}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FundProjectionScreenOutlined /> Show</span>}
              >
                {profileStore.selectedChildProfile.fVideo}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><VideoCameraFilled /> Cartoon</span>}
              >
                {profileStore.selectedChildProfile.fCartoon}
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FireOutlined /> Favourite Temperature</span>}
              >
                {profileStore.selectedChildProfile.fTemperature} ℃
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><ClockCircleOutlined /> Average Shower Time</span>}
              >
                {profileStore.selectedChildProfile.averageShowerTime} mins
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><FireOutlined /> Temperature Lock</span>}
                span={4}
              >
                {
                  `${profileStore.selectedChildProfile.temperatureRange[0]}~
                  ${profileStore.selectedChildProfile.temperatureRange[1]} ℃`
                }
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><ClockCircleOutlined /> Timer</span>}
              >
                {profileStore.selectedChildProfile.timer} mins
              </Descriptions.Item>
              <Descriptions.Item
                label={<span><MedicineBoxFilled /> Medical Notes</span>}
                span={4}
              >
                {profileStore.selectedChildProfile.medicalNotes}
              </Descriptions.Item>
            </ChildProfileDescription>
          </ChildDetailWrapper>
          :
          null}
        );
      </>
    );
  };

  return (
    profileStore.isLogin
      ? renderChildrenProfile(
        profileStore.clientProfile?.children as ChildProfile[]
      )
      : null
  );
});

export default ChildProfileSection;
