import React, { useMemo, useEffect } from 'react';
import styled from 'styled-components';
import { message } from 'antd';
import { debounce } from 'lodash';
import {
  AudioOutlined,
  AudioMutedOutlined
} from '@ant-design/icons';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import './index.less';

const CommandWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
`;

const DD = styled.dd`
  margin-left: 5%;
`;

const ErrorMessage = (
  <>
    Invalid Voice Command
    <CommandWrapper>
      <hr />
      <dl>
        <dt>
          <span role="img" aria-label="speaker">🔊</span>
          'increase by x'
        </dt>
        <DD> - increase x°C</DD>
        <dt>
          <span role="img" aria-label="speaker">🔊</span>
          'decrease by x'
        </dt>
        <DD> - decrease x°C</DD>
        <dt>
          <span role="img" aria-label="speaker">🔊</span>
          'hotter'
        </dt>
        <DD> - increase 1°C</DD>
        <dt>
          <span role="img" aria-label="speaker">🔊</span>
          'cooler'
        </dt>
        <DD> - decrease 1°C</DD>
        <dt>
          <span role="img" aria-label="speaker">🔊</span>
          'stop'
        </dt>
        <DD> - stop voice detection</DD>
      </dl>
    </CommandWrapper>
  </>
);

interface VoiceControlBtnProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
}

function VoiceControlBtn({
  setTemperature,
  temperature
}: VoiceControlBtnProps) {
  const commands = [
    {
      command: 'increase by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.min(40, temperature + t));
      }
    },
    {
      command: 'decrease by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.max(20, temperature - t));
      }
    },
    {
      command: 'cooler',
      callback: () => {
        setTemperature(Math.max(20, temperature - 1));
      },
      isFuzzyMatch: true
    },
    {
      command: 'hotter',
      callback: () => {
        setTemperature(Math.min(40, temperature + 1));
      },
      isFuzzyMatch: true
    },
    {
      command: 'stop',
      callback: () => {
        resetTranscript();
        SpeechRecognition.stopListening();
      }
    }
  ];

  const {
    listening,
    transcript,
    resetTranscript
  } = useSpeechRecognition({ commands });

  // clearup transcripts
  const clearTranscript = useMemo(() => debounce((ts: string) => {
    const validation
      = /^(increase|decrease|cooler|hotter|stop)/g;
    if (listening && ts && !ts.match(validation))
      message.error(ErrorMessage, 3);
    if (listening) resetTranscript();
  }, 2000), [resetTranscript, listening]);

  useEffect(() => {
    return clearTranscript(transcript);
  }, [transcript, clearTranscript]);

  // check if browser supports speech api
  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return null;
  }

  return (
    <div className='voice-btn-wapper'>
      {!listening
        ? (
          <button
            type='button'
            onClick={(): void => {
              resetTranscript();
              SpeechRecognition.startListening({ continuous: true });
            }}
          >
            <AudioMutedOutlined />
          </button>
        )
        : (
          <>
            <button
              type='button'
              className='active'
              onClick={SpeechRecognition.stopListening}
            >
              <AudioOutlined />
            </button>
            <p>{transcript}</p>
          </>
        )}
    </div>
  );
}

export default VoiceControlBtn;
